import React, { useState } from "react";
import "./Mockup.css";
import { Form, Stack, Spinner } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/downloadTemplate";
import useToast from "../../hooks/useToast";
import * as XLSX from "xlsx";
import config from "./config.json";
import Loader from "../../components/Loader/Loader";

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [errorModalShow, setErrorModalShow] = useState(false);
  const { showErrorToast } = useToast();
  const [isValidating, setIsValidating] = useState(false);
  const [errors, setErrors] = useState([]);

  // Function to validate the uploaded file
  const validateFile = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsBinaryString(file);
  
      reader.onload = (e) => {
        const workbook = XLSX.read(e.target.result, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        const sheetData = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName], {
          defval: "", // Ensures empty cells are captured
        });
  
        if (sheetData.length === 0) {
          reject(["The uploaded file is empty."]);
          return;
        }
  
        // Extract headers from first row
        const fileHeaders = Object.keys(sheetData[0]);
  
        // üî• **NEW: Check for duplicate headers**
        const headerCounts = {};
        fileHeaders.forEach((header) => {
          headerCounts[header] = (headerCounts[header] || 0) + 1;
        });
  
        const duplicateHeaders = Object.keys(headerCounts).filter(
          (header) => headerCounts[header] > 1
        );
  
        if (duplicateHeaders.length > 0) {
          reject([`Duplicate headers found: ${duplicateHeaders.join(", ")}`]);
          return;
        }
  
        // Determine matching operation based on headers
        let matchedOperation = null;
        for (const [key, value] of Object.entries(config.MassUpload)) {
          if (value.fileHeaders.every((header) => fileHeaders.includes(header))) {
            matchedOperation = key;
            break;
          }
        }
  
        if (!matchedOperation) {
          reject(["File headers do not match any expected template."]);
          return;
        }
  
        const expectedHeaders = config.MassUpload[matchedOperation].fileHeaders;
        const mandatoryAttributes = config.MassUpload[matchedOperation].mandatoryAttributes;
  
        // Validate missing headers
        const missingHeaders = expectedHeaders.filter((header) => !fileHeaders.includes(header));
        if (missingHeaders.length > 0) {
          reject([`Missing required headers: ${missingHeaders.join(", ")}`]);
          return;
        }
  
        // Validate mandatory fields are not empty
        const validationErrors = [];
        sheetData.forEach((row, index) => {
          mandatoryAttributes.forEach((field) => {
            if (!row[field] || row[field].toString().trim() === "") {
              validationErrors.push(`Row ${index + 2}: "${field}" is required but is empty.`);
            }
          });
        });
  
        if (validationErrors.length > 0) {
          reject(validationErrors);
        } else {
          resolve();
        }
      };
  
      reader.onerror = () => reject(["File reading failed."]);
    });
  };
  

  const handleFileUpload = (files) => {
    if (files.length === 0) return;

    setIsValidating(true);
    validateFile(files[0])
      .then(() => {
        setErrors([]);
        setIsValidating(false);
      })
      .catch((validationErrors) => {
        setErrors(validationErrors);
        setIsValidating(false);
      });
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              outline: "none",
              borderRadius: "8px",
            }}
            onChange={(e) => downloadTemplate(showErrorToast)(e.target.value)}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>
        </Stack>

        {/* Drag and Drop File Upload */}
        <FileUpload fileTypes={["XLSX"]} multiple={false} onUpload={handleFileUpload} />

        {/* Show loader while validating */}
        {isValidating && (
          <Loader />
        )}

        {/* Show Content Error Link if validation fails */}
        {errors.length > 0 && (
          <CustomButton
            variant="danger"
            text="Content Error"
            onClick={() => setErrorModalShow(true)}
          />
        )}

        {/* Submit Button */}
        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="Background" />
          <CustomButton variant="primary ms-auto" onClick={() => setModalShow(true)} text="Submit" />
        </Stack>
      </Stack>

      {/* Validation Errors Modal */}
      <CustomModal
        show={errorModalShow}
        onHide={() => setErrorModalShow(false)}
        title="Validation Errors"
        footerButtons={[
          { label: "Close", variant: "danger", onClick: () => setErrorModalShow(false) },
        ]}
      >
        <ul>
          {errors.map((error, index) => (
            <li key={index}>{error}</li>
          ))}
        </ul>
      </CustomModal>

      {/* Confirmation Modal */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Custom Modal Title"
        footerButtons={[
          { label: "Cancel", variant: "danger", onClick: () => setModalShow(false) },
          { label: "Confirm", variant: "success", onClick: () => alert("Confirmed!") },
        ]}
      >
        <p>This is a reusable modal component with dynamic content.</p>
      </CustomModal>
    </>
  );
};

export default MassUpload;
